<template>
  <v-bottom-navigation :value="activeBtn" grow background-color="green accent-3" color="white">
    <v-spacer></v-spacer>

    <v-btn to="/home">
      <span>בית</span>
      <v-icon>mdi-home</v-icon>
    </v-btn>

    <v-btn to="/scanner">
      <span>סריקה</span>
      <v-icon>mdi-barcode-scan</v-icon>
    </v-btn>

    <v-btn>
      <span>צור קשר</span>
      <v-icon>mdi-alert-octagon</v-icon>
    </v-btn>
    <v-spacer></v-spacer>

    <v-btn v-if="!user" to="/login">
      <span>התחבר</span>
      <v-icon>mdi-login</v-icon>
    </v-btn>

    <v-btn v-if="user" @click="logout">
      <span>התנתק</span>
      <v-icon>mdi-logout</v-icon>
    </v-btn>
  </v-bottom-navigation>
</template>

<script>
import firebase from "firebase";

export default {
      methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace("login");
        });
    }
  },

  data() {
    return {
      user: null,
      activeBtn: 1
    };
  },
  created: function() {
    var vm = this;
    firebase.auth().onAuthStateChanged(function(user) {
      if (user) {
        vm.user = user;
      } else {
        vm.user = null;
      }
    });
  }
};
</script>

<style scoped>
.v-item-group.v-bottom-navigation .v-btn {
  flex: 0 1 auto;
  font-size: 1rem;
  height: inherit;
}

.theme--light.v-bottom-navigation .v-btn:not(.v-btn--active) {
  color: #33691e !important;
}
</style>