<template>
  <div class="text-center">
    <h3>התחבר</h3>

    <v-layout justify-center>
      <v-col cols="12" sm="6" md="3" lg="3">
        <v-text-field
          autofocus
          type="email"
          v-model="email"
          :rules="emailRules"
          required
          color="success"
          persistentHint
          clearable
          prepend-inner-icon="mdi-email"
          label="דואר אלקטרוני"
        ></v-text-field>
      </v-col>
    </v-layout>

    <v-layout justify-center>
      <v-col cols="12" sm="6" md="3" lg="3">
        <v-text-field
          :rules="[rules.required, rules.min]"
          type="password"
          v-model="password"
          color="success"
          persistentHint
          clearable
          prepend-inner-icon="mdi-lock-question text-success"
          label="סיסמה"
        ></v-text-field>
      </v-col>
    </v-layout>

    <v-btn @click="login" class="ma-2" tile outlined color="success">
      <v-icon left>mdi-login</v-icon>התחבר
    </v-btn>
    <br />
    <br />
    <v-btn style="width: 120px;" to="/send-email-reset" class="ma-0" tile outlined color="success">
      <v-icon left>mdi-lock-question</v-icon>אפס סיסמה
    </v-btn>
    <v-btn style="width: 120px;" to="/sing-up" class="ma-0" tile outlined color="success">
      <v-icon left>mdi-login</v-icon>הרשמה
    </v-btn>
  </div>
</template>

<script>
import firebase from "firebase";
export default {
  name: "login",
  data() {
    return {
      email: "",
      password: "",
      emailRules: [
        v => !!v || "אנא הכנס כתובת דואר אלקטרוני",
        v => /.+@.+/.test(v) || "אנא הכנס כתובת דואר אלקטרוני חוקי"
      ],
      rules: {
        required: value => !!value || "הכנס סיסמה",
        min: v => v.length >= 8 || "הסיסמה חייבת להכיל 8 תווים",
        emailMatch: () => "The email and password you entered don't match"
      }
    };
  },
  methods: {
    changeRTL() {
      this.$vuetify.rtl = true;
    },

    //log in user name and password
    login: function() {
      // this.$router.replace('home');
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(
          () => {
            this.$router.replace("/home");
          },
          err => {
            alert("opss." + err.message);
          }
        );
    }
    //end login username and password
  }
};
</script>

<style scoped>
.v-input__control {
    color: green;
}
</style>