<template>
  <div class="text-center">
    <br />
    <h3>אפס סיסמה</h3>
    <v-layout justify-center>
      <v-col cols="12" sm="6" md="3" lg="3">
        <v-text-field
          autofocus
          type="email"
          v-model="email"
          :rules="emailRules"
          required
          color="success"
          persistentHint
          clearable
          prepend-inner-icon="mdi-email"
          label="דואר אלקטרוני"
        ></v-text-field>
      </v-col>
    </v-layout>
    <v-btn style="width: 120px;" @click="sendMailReset" class="ma-0" tile outlined color="success">
      <v-icon left>mdi-lock-question</v-icon>אפס סיסמה
    </v-btn>

    <br />
    <br />
    <v-btn style="width: 120px;" to="/login" class="ma-0" tile outlined color="success">
      <v-icon left>mdi-login</v-icon>התחבר
    </v-btn>
    <v-btn style="width: 120px;" to="/sing-up" class="ma-0" tile outlined color="success">
      <v-icon left>mdi-login</v-icon>הרשמה
    </v-btn>
  </div>
</template>


<script>
import firebase from "firebase";

export default {
  name: "sendMailReset",
  data() {
    return {
      email: "",
      emailRules: [
        v => !!v || "אנא הכנס כתובת דואר אלקטרוני",
        v => /.+@.+/.test(v) || "אנא הכנס כתובת דואר אלקטרוני חוקי"
      ]
    };
  },
  methods: {
    sendMailReset: function() {
      const auth = firebase.auth();
      let emailAddress = this.email;

      auth
        .sendPasswordResetEmail(emailAddress)
        .then(() => {
          alert("eimail send");
        })
        .catch(error => {
          alert("error" + error);
        });
    }
  }
};
</script>
<style scoped>
</style>